export function getDot(points, scale) {
    var ps = points.slice(-2);
    var _a = ps[0], xa = _a.x, ya = _a.y;
    var _b = ps[1], xb = _b.x, yb = _b.y;
    xa = xa / scale;
    ya = ya / scale;
    xb = xb / scale;
    yb = yb / scale;
    var x = xb - xa;
    var y = yb - ya;
    var len = Math.pow(Math.pow(x, 2) + Math.pow(y, 2), 0.5);
    if (len > 50) {
        var xc = (1 - 40 / len) * x + xa;
        var yc = (1 - 40 / len) * y + ya;
        return {
            x: xc,
            y: yc,
        };
    }
    var allX = xa + xb;
    var allY = ya + yb;
    return {
        x: allX / 2,
        y: allY / 2,
    };
}
