import ReactDOM from 'react-dom';
import classnames from 'classnames';
import './style.scss';
var cls = 'bpm-flowchart-mask';
export function useMask(props) {
    var className = props.className, children = props.children;
    var clss = classnames(cls, className);
    var container = document.createElement('div');
    container.className = clss;
    var background = document.createElement('div');
    background.className = cls + "-background";
    container.appendChild(background);
    var wrapper = document.createElement('div');
    wrapper.className = cls + "-wrapper";
    container.appendChild(wrapper);
    var close = function () {
        ReactDOM.unmountComponentAtNode(wrapper);
        document.body.removeChild(container);
    };
    background.onclick = close;
    document.body.appendChild(container);
    ReactDOM.render(children(close), wrapper, function () {
        container.className = classnames(clss, cls + "-active");
    });
}
